import React from 'react';
import ExercisesContext from 'contexts/exercises';

import styles from './ExerciseList.module.scss';
import { ChangingMetronomeExercise } from '../../exercises/ChangingMetronomeExercise/ChangingMetronomeExercise';

export const exerciseComponentByType: {[key: string]: React.FC} = {
    changingMetronome: ChangingMetronomeExercise,
}

export const ExerciseList: React.FC = () => {
    const exercises = React.useContext(ExercisesContext);
    return (
        <div className={styles.base}>
            {exercises.map(exercise => (
                <React.Fragment key={exercise}>{exerciseComponentByType[exercise]({})}</React.Fragment>
            ))}
        </div>
    );
};
