import React from 'react';
import classNames from 'classnames';

import { NoteSize } from 'types';

import cancelImage from './images/cancel.svg';

import styles from './Note.module.scss';

const images = {
    [NoteSize.Fours]: require('./images/quarter.svg'),
    [NoteSize.Eights]: require('./images/eights.svg'),
    [NoteSize.Sixteens]: require('./images/sixteens.svg'),
    [NoteSize.Triplets]: require('./images/triplet.svg'),
}

export interface NoteProps {
    draggableRef?: any;
    note: NoteSize;
    draggingProps?: any;
    isDraggingAway?: boolean;
    blinkIndex?: number;
}

const Note: React.FC<NoteProps> = (props) => {
    const image = props.isDraggingAway ? cancelImage : images[props.note];
    return (
        <div
            key={props.blinkIndex}
            ref={props.draggableRef}
            className={classNames(styles.base, {[styles.base__blink]: props.blinkIndex !== -1})}
            {...props.draggingProps}
        >
            <img alt='' src={image} />
        </div>
    );
}

export default Note;
