export class BaseHandler {
    allowKeys: string[] = [];
    anyKey: boolean = false;
    startTime: number = 0;

    setStartTime(startTime: number) {
        this.startTime = startTime;
    }
    beforeRun(): Promise<void> {
        return Promise.resolve();
    }
    run(): Promise<void> {
        return Promise.resolve();
    }
    afterRun(): Promise<void> {
        return Promise.resolve();
    }
    onKeyboardEvent(time: number, key: string) {}
    onMouseEvent(time: number) {}
}

export class ExerciseManager {
    private handlers: BaseHandler[];
    public startTime: number = 0;
    public endTime: number = 0;
    public inProgress: boolean = false;

    constructor() {
        this.handlers = [];
    }

    addHandler(handler: BaseHandler) {
        this.handlers.push(handler);
        return this;
    }

    async start() {
        this.inProgress = true;
        this.startTime = new Date().getTime();

        Promise.all(this.handlers.map(handler => handler.setStartTime(this.startTime)));
        await Promise.all(this.handlers.map(handler => handler.beforeRun()));
        await Promise.all(this.handlers.map(handler => handler.run()));
        await Promise.all(this.handlers.map(handler => handler.afterRun()));
    }
}


export class SoundHandler extends BaseHandler {
    context: AudioContext;
    gain: GainNode;
    oscillator: OscillatorNode;
    clicks: number[];
    preClicksCount: number;

    constructor(clicks: number[], preClicksCount: number) {
        super();
        this.clicks = clicks;
        this.preClicksCount = preClicksCount;
        // @ts-ignore
        const AudioContextClass = window.AudioContext || window.webkitAudioContext; 

        this.context = new AudioContextClass();
        this.oscillator = this.context.createOscillator();
        this.oscillator.type = 'sine';
        this.oscillator.frequency.value = 1000;

        this.gain = this.context.createGain();
        this.gain.gain.value = 0;
        this.gain.connect(this.context.destination);
        this.oscillator.connect(this.gain);
    }

    beforeRun(): Promise<void> {
        if (this.clicks.length === 0) {
            return Promise.resolve();
        }
        this.clicks.forEach(click => {
            this.gain.gain.cancelScheduledValues(click);
            this.gain.gain.setValueAtTime(0, click);

            this.gain.gain.linearRampToValueAtTime(1, click + .001);
            this.gain.gain.linearRampToValueAtTime(0, click + .001 + .01);
        })
        this.oscillator.start(0);
        return new Promise(resolve => setTimeout(resolve, this.clicks[this.preClicksCount - 1] * 1000));
    }

    run(): Promise<void> {
        const beforeRunTime = this.clicks[this.preClicksCount - 1]
        return new Promise(resolve => setTimeout(resolve, (this.clicks[this.clicks.length - 1] - beforeRunTime) * 1000 + 300));
    }
}

export type CallbackFunction = (time: number) => void
export type TickCallbackFunction = (time: number, index: number) => void


export class TickHandler extends BaseHandler {
    callback: TickCallbackFunction;
    clicks: number[];

    constructor(clicks: number[], callback: TickCallbackFunction) {
        super();
        this.clicks = clicks;
        this.callback = callback;
    }

    beforeRun(): Promise<void> {
        if (this.clicks.length > 0) {
            this.clicks.forEach((click, index) => setTimeout(() => this.callback(click, index), click * 1000))
        }
        return Promise.resolve();
    }
}


export const TYPICAL_DELAY = 50;

export class ClickHandler extends BaseHandler {
    anyKey: boolean = true;
    callback: CallbackFunction;

    constructor(callback: CallbackFunction) {
        super();
        this.callback = callback;
    }

    run(): Promise<void> {
        document.addEventListener('keydown', this.handleKeydown);
        document.addEventListener('mousedown', this.handleMouseDown);
        return Promise.resolve();
    }

    afterRun(): Promise<void> {
        document.removeEventListener('keydown', this.handleKeydown);
        document.removeEventListener('mousedown', this.handleMouseDown);
        return Promise.resolve();
    }

    private handleKeydown = (e: KeyboardEvent) => {
        const time = new Date().getTime();
        if (this.anyKey || this.allowKeys.includes(e.key)) {
            this.onKeyboardEvent(time, e.key);
        }
    }

    private handleMouseDown = () => {
        const time = new Date().getTime();
        this.onMouseEvent(time);
    }

    onKeyboardEvent(time: number, key: string) {
        this.callback(time - TYPICAL_DELAY);
    }

    onMouseEvent(time: number) {
        this.callback(time - TYPICAL_DELAY);
    }
}
