import React from 'react';
import uniqBy from 'lodash/uniqBy';
import isArray from 'lodash/isArray';

import { NotesSequence, NotesSequenceWrapper, NotesSource} from 'components/NotesSequence/NotesSequence';
import TicksCanvas from 'components/UserClicksCanvas/UserClicksCanvas';
import NumberInput from 'components/NumberInput/NumberInput';
import { ExerciseManager, SoundHandler, TickHandler } from 'exercises/base';
import { ChangingMetronomeClickHandler } from 'exercises/ChangingMetronomeExercise/changingMetronome';
import { UserClick } from 'exercises/ChangingMetronomeExercise/types';
import { clicksFromNotes, getPlayingNotesIndexes } from 'utils/sizes';

import styles from './ChangingMetronomeExercise.module.scss';
import { NoteSize } from 'types';

const BPM_LOCALSTORAGE_KEY = 'saved_bpm';
const SEQUENCE_LOCALSTORAGE_KEY = 'saved_sequence';

enum ExerciseState {
    NOT_STARTED,
    COUNTDOWN,
    RUNNING,
    FINISHED,
    FAILED_TO_START,
}

export const ChangingMetronomeExercise: React.FC = () => {
    const notes = [
        NoteSize.Fours,
        NoteSize.Eights,
        NoteSize.Triplets,
        NoteSize.Sixteens,
    ];
    const savedBpm = parseInt(localStorage.getItem(BPM_LOCALSTORAGE_KEY) || '60', 10);
    let initialSequence;
    try {
        initialSequence = JSON.parse(localStorage.getItem(SEQUENCE_LOCALSTORAGE_KEY)!);
        if (!isArray(initialSequence)) {
            throw Error();
        }
    } catch(e) {
        initialSequence = [...notes];
    }
    const [ userClicks, setUserClicks ] = React.useState<UserClick[]>([]);
    const [ clicks, setClicks ] = React.useState<number[]>([]);
    const [ sequence, setSequence ] = React.useState<NoteSize[]>(initialSequence);
    const [ noteIndexes, setNoteIndexes] = React.useState<number[]>([]);
    const [ bpm, setBpm ] = React.useState<number>(savedBpm);
    const [ countdown, setCountdown ] = React.useState<number>(0);
    const [ state, setState ] = React.useState<ExerciseState>(ExerciseState.NOT_STARTED);
    const [ playingIndex, setPlayingIndex ] = React.useState(0);

    const handleUserClicks = (newUserClicks: UserClick[]) => {
        setUserClicks(newUserClicks);
    }

    const handleTick = (time: number, index: number) => {
        setPlayingIndex(index - 4);
        if (index < 4) {
            setCountdown(index + 1);
        }
        if (index === 3) {
            setState(ExerciseState.RUNNING);
        }
    }

    const runExercise = async () => {
        if (state === ExerciseState.COUNTDOWN || state === ExerciseState.RUNNING) {
            return;
        }
        localStorage.setItem(BPM_LOCALSTORAGE_KEY, bpm.toString());
        localStorage.setItem(SEQUENCE_LOCALSTORAGE_KEY, JSON.stringify(sequence));
        if (sequence.length === 0) {
            setState(ExerciseState.FAILED_TO_START);
            return;
        }
        const clicks = clicksFromNotes([NoteSize.Fours, ...sequence], bpm);
        setNoteIndexes(getPlayingNotesIndexes(sequence));
        
        setUserClicks([]);
        setState(ExerciseState.COUNTDOWN);
        setClicks(clicks);

        try {
            const manager = new ExerciseManager();
            manager.addHandler(new SoundHandler(clicks, 4));
            manager.addHandler(new TickHandler(clicks, handleTick));
            manager.addHandler(new ChangingMetronomeClickHandler(clicks, handleUserClicks));
            await manager.start();
        } catch (e) {
            setState(ExerciseState.FAILED_TO_START);
            return;
        }

        setState(ExerciseState.FINISHED);
    };

    return (
        <div className={styles.base}>
            <div key={playingIndex} className={styles.blink} />
            <NotesSequenceWrapper sequence={sequence} onSequenceChange={setSequence} notes={notes}>
                <div className={styles.notes}>
                    <div className={styles.caption}>Notes (drag to sequence)</div>
                    <NotesSource notes={notes} />
                </div>
                <div className={styles.bpm}>
                    <div className={styles.caption}>BPM</div>
                    <NumberInput onChanged={setBpm} value={bpm} />
                </div>
                <div className={styles.sequence}>
                    <div className={styles.caption}>Sequence (size: 4/4)</div>
                    <NotesSequence
                        isPlaying={state === ExerciseState.COUNTDOWN || state === ExerciseState.RUNNING}
                        sequence={sequence}
                        noteIndex={noteIndexes[playingIndex]}
                        playingIndex={playingIndex}
                    />
                </div>
            </NotesSequenceWrapper>
            
            <div className={styles.action}>
                {
                    state === ExerciseState.NOT_STARTED &&
                    <>
                        <div className={styles.info}>Train your ability to keep rhythm</div>
                        <div className={styles.info}>Manage sequence, choose bpm and click start</div>
                    </>
                }
                {
                    state === ExerciseState.COUNTDOWN &&
                    <div className={styles.info}>{countdown}</div>
                }
                {
                    (state === ExerciseState.RUNNING || state === ExerciseState.COUNTDOWN) && 
                    <div className={styles.info}>Tap or click exactly when metronome click</div>
                }
                {
                    (state === ExerciseState.RUNNING || state === ExerciseState.FINISHED) &&
                    <div className={styles.ticks}>
                        <TicksCanvas idealClicks={clicks} realClicks={userClicks} from={4} />
                    </div>
                }
                {
                    state === ExerciseState.FINISHED && userClicks.length !== 0 && 
                    <>
                        <div className={styles.info}>
                            Missed notes:
                            <span className={styles.score}>
                                {Math.max(clicks.length - uniqBy(userClicks, c => c.desiredClick).length - 4, 0)}
                            </span>
                            Average delta:
                            <span className={styles.score}>
                                {Math.round(1000 * userClicks.map(u => Math.abs(u.delta)).reduce((prev, curr) => prev + curr) / userClicks.length)}ms
                            </span>
                        </div>
                        <div className={styles.info}>
                            Cross deltas (this supposed to be close to zero):
                            <span className={styles.score}>
                                {userClicks.map((userClick, i) => {
                                    if (i + 1 !== userClicks.length) {
                                        return Math.round(1000 * (userClick.delta - userClicks[i + 1].delta)) + 'ms';
                                    }
                                    return null;
                                }).filter(Boolean).join(', ')}
                            </span>
                        </div>
                    </>
                }
                {
                    state === ExerciseState.FINISHED && userClicks.length === 0 &&
                    <div className={styles.info}>No results</div>
                }
                {
                    (state === ExerciseState.NOT_STARTED || state === ExerciseState.FINISHED) &&
                    <button className={styles.start} onClick={runExercise}>START</button>
                }
                {
                    state === ExerciseState.FAILED_TO_START &&
                    <button className={styles.start} onClick={runExercise}>TRY START AGAIN</button>
                }
                {
                    state === ExerciseState.FAILED_TO_START &&
                    <div className={styles.info}>Unfortunately exercise cannot be run on your device</div>
                }
            </div>
        </div>
    );
};

