import React from 'react';

import { UserClick } from 'exercises/ChangingMetronomeExercise/types';

import styles from './UserClicksCanvas.module.scss';

export interface UserClicksCanvasProps {
    idealClicks: number[];
    realClicks: UserClick[];
    from: number;
}

export default class UserClicksCanvas extends React.Component<UserClicksCanvasProps> {
    canvas: React.RefObject<HTMLCanvasElement>;

    constructor(props: UserClicksCanvasProps) {
        super(props);

        this.canvas = React.createRef();
    }

    componentDidMount() {
        window.addEventListener('resize', this.onResize);
        this.onResize();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.onResize);
    }

    componentDidUpdate() {
        this.draw();
    }

    onResize = () => {
        if (this.canvas.current) {
            this.canvas.current.height = this.canvas.current.parentElement!.clientHeight;
            this.canvas.current.width = this.canvas.current.parentElement!.clientWidth;
            this.draw();
        }
    }

    draw = () => {
        if (!this.canvas.current) {
            return;
        }
        const context = this.canvas.current.getContext('2d');
        const { width, height } = this.canvas.current
        if (!context) {
            return;
        }
        context.clearRect(0, 0, width, height);
        const min = this.props.idealClicks[this.props.from] - 0.5;
        const total = this.props.idealClicks[this.props.idealClicks.length - 1] - min + 0.5;
        this.props.idealClicks.forEach(click => 
            this.drawLine(context, width * (click - min) / total, 0, height / 2, '#4287f5')
        );
        this.props.realClicks.forEach(click => 
            this.drawLine(context, width * (click.actualClick - min) / total, height / 2, height, this.getColorByDelta(click.delta))
        );
    }

    drawLine = (context: CanvasRenderingContext2D, x: number, y: number, height: number, color: string) => {
        const roundX = Math.round(x);
        context.beginPath();
        context.strokeStyle = color;
        context.moveTo(roundX, y);
        context.lineTo(roundX, y + height);
        context.stroke();
        context.closePath();
    }

    getColorByDelta = (delta: number) => {
        if (delta > 0.2) {
            return '#FF0000';
        }
        if (delta > 0.1) {
            return '#FFFF00';
        }
        return '#00FF00';
    }

    render() {
        return <div className={styles.base}><canvas ref={this.canvas}></canvas></div>;
    }
}
