import React from 'react';
import { DragDropContext, Draggable, Droppable, DroppableProvided, DropResult, DroppableStateSnapshot, DraggableProvided, DraggableStateSnapshot } from 'react-beautiful-dnd';
import classNames from 'classnames';

import Note from 'components/Note/Note';
import { NoteSize } from 'types';

import styles from './NotesSequence.module.scss';

function reorder<T>(list: T[], startIndex: number, endIndex: number): T[] {
    const result = [...list];
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
};


enum ListDroppableId {
    NOTES = 'notes',
    SEQUENCE = 'sequence',
}

export interface NotesSequenceState {
    notes: NoteSize[];
    sequence: NoteSize[];
}

export interface NotesSequenceWrapperProps {
    notes: NoteSize[];
    sequence: NoteSize[];
    onSequenceChange: (sequence: NoteSize[]) => void;
}

export const NotesSequenceWrapper: React.FC<NotesSequenceWrapperProps> = (props) => {
    const handleDragEnd = (result: DropResult) => {
        const { source, destination } = result;
        
        if (source.droppableId === ListDroppableId.SEQUENCE) {
            if (!destination || destination.droppableId !== ListDroppableId.SEQUENCE) {
                const sequence = [...props.sequence]
                sequence.splice(source.index, 1)
                return props.onSequenceChange(sequence);
            }
            return props.onSequenceChange(reorder(props.sequence, source.index, destination.index));
        }
        if (source.droppableId === ListDroppableId.NOTES && destination?.droppableId === ListDroppableId.SEQUENCE) {
            const sequence = [...props.sequence]
            sequence.splice(destination.index, 0, props.notes[source.index])
            return props.onSequenceChange(sequence);
        }
    }
    
    return (
        <DragDropContext onDragEnd={handleDragEnd}>
            {props.children}
        </DragDropContext>
    )
}

export interface NotesSourceProps {
    notes: NoteSize[];
}

export const NotesSource: React.FC<NotesSourceProps> = (props) => {
    return (
        <Droppable droppableId={ListDroppableId.NOTES} direction='horizontal' isDropDisabled>
            {(provided: DroppableProvided) => (
                <div ref={provided.innerRef} className={styles.source} {...provided.droppableProps} style={{transitionDuration: `0.001s`,}}>
                    {props.notes.map((note, index) => (
                         <Draggable key={note} draggableId={`note_${note}`} index={index}>
                         {(draggable, snapshot) => {
                             return (
                                 <>
                                    <Note 
                                        draggableRef={draggable.innerRef}
                                        note={note}
                                        draggingProps={{
                                            ...draggable.draggableProps,
                                            ...draggable.dragHandleProps,
                                        }}
                                    />
                                    {
                                        snapshot.isDragging &&
                                        <Note note={note} />
                                    }
                                 </>
                             );
                         }}
                     </Draggable>
                    ))}
                    <div style={{display: 'none'}}>{provided.placeholder}</div>
                </div>
            )}
        </Droppable>
    );
}

export interface NotesSequenceProps {
    sequence: NoteSize[];
    isPlaying?: boolean;
    playingIndex: number;
    noteIndex: number;
}

export const NotesSequence: React.FC<NotesSequenceProps> = (props) => {
    if (props.isPlaying) {
        return (
            <div className={styles.sequence}>
                {props.sequence.map((note, index) => (
                    <Note
                        key={index}
                        note={note}
                        blinkIndex={index === props.noteIndex ? props.playingIndex : -1}
                    />
                ))}
            </div>
        );
    }
    return (
        <Droppable droppableId={ListDroppableId.SEQUENCE} direction='horizontal'>
            {(provided: DroppableProvided, snapshot: DroppableStateSnapshot) => (
                <div
                    ref={provided.innerRef}
                    className={classNames(styles.sequence, {
                        [styles.sequence__over]: snapshot.isDraggingOver,
                    })}
                >
                    {props.sequence.map((note, index) => (
                        <Draggable key={index} draggableId={`sequence_note_${index}`} index={index}>
                            {(draggable: DraggableProvided, snapshot: DraggableStateSnapshot) => {
                                return (
                                    <Note 
                                        draggableRef={draggable.innerRef}
                                        note={note}
                                        isDraggingAway={snapshot.isDragging && !snapshot.draggingOver}
                                        draggingProps={{
                                            ...draggable.draggableProps,
                                            ...draggable.dragHandleProps,
                                        }}
                                    />
                                );
                            }}
                        </Draggable>
                    ))}
                    <div style={{display: 'none'}}>{provided.placeholder}</div>
                </div>
            )}
        </Droppable>
    );
}

