import React from 'react';

import styles from './NumberInput.module.scss';

interface NumberInputProps {
    value: number;
    onChanged: (value: number) => void;
}

const MIN_VALUE = 10
const MAX_VALUE = 500

const NumberInput: React.FC<NumberInputProps> = (props: NumberInputProps) => {
    const [value, setValue] = React.useState(props.value);

    const changeValue = (newValue: number) => {
        const nextValue = Math.max(Math.min(newValue, MAX_VALUE), MIN_VALUE);
        setValue(nextValue);
        props.onChanged(nextValue);
    }

    const onInputChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
        changeValue(parseInt(e.target.value)); 
    }

    const onWheel = (e: React.WheelEvent<HTMLInputElement>) => {
        changeValue(value + (e.deltaY > 0 ? -1 : 1));
    }

    return (
        <div className={styles.base}>
            <div className={styles.button} onClick={changeValue.bind(null, value - 10)}>-10</div>
            <div className={styles.button} onClick={changeValue.bind(null, value - 5)}>-5</div>
            <div className={styles.button} onClick={changeValue.bind(null, value - 1)}>-1</div>
            <input
                className={styles.value}
                min={MIN_VALUE} max={MAX_VALUE} type='number'
                onChange={onInputChanged}
                onBlur={onInputChanged}
                onWheel={onWheel}
                value={value}
            />
            <div className={styles.button} onClick={changeValue.bind(null, value + 1)}>+1</div>
            <div className={styles.button} onClick={changeValue.bind(null, value + 5)}>+5</div>
            <div className={styles.button} onClick={changeValue.bind(null, value + 10)}>+10</div>
        </div>
    );
};

export default NumberInput;