import { ClickHandler } from 'exercises/base';
import { UserClick } from 'exercises/ChangingMetronomeExercise/types';

export interface ChangingMetronomeParams {
    clicks: number[];
}

export type onUpdate = (clicks: UserClick[]) => void;

export class ChangingMetronomeClickHandler extends ClickHandler {
    clicks: number[];
    userClicks: number[];

    constructor(clicks: number[], callback: onUpdate) {
        super((time) => {
            this.userClicks.push((time - this.startTime) / 1000);
            callback(this.getUserClicks());
        });

        this.clicks = clicks;
        this.userClicks = [];
    }

    getUserClicks = () => {
        const clicks: UserClick[] = [];
        this.userClicks.forEach(click => {
            const delta = this.clicks.reduce(function(minDelta, currentValue) {
                return (Math.abs(minDelta) > Math.abs(currentValue - click) ? currentValue - click : minDelta);
            }, Infinity);
            clicks.push({
                desiredClick: click + delta,
                actualClick: click,
                delta,
            })
        });
        return clicks;
    }
}
