import { NoteSize } from 'types';

export const clicksBySize = {
    [NoteSize.Fours]: Array.apply(0, Array(4)).map<number>((_: any, index: number) => (index / 4)),
    [NoteSize.Eights]: Array.apply(0, Array(8)).map<number>((_: any, index: number) => (index / 8)),
    [NoteSize.Triplets]: Array.apply(0, Array(12)).map<number>((_: any, index: number) => (index / 12)),
    [NoteSize.Sixteens]: Array.apply(0, Array(16)).map<number>((_: any, index: number) => (index / 16)),
}

export function clicksFromNotes(notes: NoteSize[], bpm: number): number[] {
    const ticksPerSecond = 4 * 60 / bpm;

    let clicks: number[] = [];
    notes.forEach((note, index) => {
        clicks = [...clicks, ...clicksBySize[note].map(click => ticksPerSecond * (click + index * 1))];
    })
    return clicks;
}

export function getPlayingNotesIndexes(notes: NoteSize[]): number[] {
    let indexes: number[] = [];
    notes.forEach((note, index) => {
        clicksBySize[note].forEach(() => {
            indexes.push(index);
        })
    })
    return indexes;
}
